<template>
  <div class="game" align="center"> 
    
    <h1>Crossfair 🐶 </h1>
    <h3> Карта в руке: {{role}}</h3>
 
    
    <h3>Список игроков, отметьте для выстрела:</h3>

    <ul>
      <li><div v-for="player in players" :key="player.index" v-on:click="chshoot(player.index)"> 
      <input type="radio" v-bind:id="player.index" name="gender" v-bind:value="player.index" v-model="shoot">
      <label v-bind:for="player.index">{{player.index + 1}}. {{player.name}} - {{player.role}} <m v-show="!player.alive">💀</m> </label><br>
      </div></li>
    </ul>
  <h3>выберете  сторону:</h3>
  <ul>

  <li><input type="radio" v-on:click="chrole('👮‍♂️')" id="agent" name="gender" value="male">
  <label for="agent">агент 👮‍♂️</label></li>
  <li><input type="radio" v-on:click="chrole('🐗')" id="boss" name="gender" value="female">
  <label for="boss">босс 🐗</label></li>
  <li><input type="radio" v-on:click="chrole('🌰')" id="secret" name="gender" value="other">
  <label for="secret">неизвестно 🌰</label></li>
  <li><input type="radio" v-on:click="chrole('😐')" id="npc" name="gender" value="other">
  <label for="npc">прохожий 😐</label></li>
  </ul>
    <p>Имя: 
      <input v-model="nameform" placeholder="vasya">
    </p>
    <button v-on:click="chname()" class="btn btn-primary" >изменить имя</button>
    <h4 v-show="losers.length != 0">Список победителей:</h4>
    <ul>
      <li v-for="winer in winers" :key="winer">
        {{winer[0] + 1}}. {{winer[1]}}
      </li>
    </ul>
    <h4 v-show="losers.length != 0">Список проигравших:</h4>
    <ul>
      <li v-for="loser in losers" :key="loser">
        {{loser[0] + 1}}. {{loser[1]}}
      </li>
    </ul>
</div>
</template>

<script>
console.log("fddf")
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },  
  data: function() {
  return {
    role: "Cервер недоступен",
    players: [],
    nameform: "",
    losers: [],
    winers: [],
    shoot: 0
    };
  },
    created: function () {
    // `this` указывает на экземпляр vm
    this.$options.sockets.onmessage = (data) => {
      console.log(data);
      data = JSON.parse( data.data)
      if (data['cm'] == 'state'){
         
        this.role = data.state.role
        for(let x = 0; x<data.users.length;x++)
        {
          data.users[x].index = x
        }
        this.players = data.users
        this.winers = data.winers
        this.losers = data.losers
        this.shoot = data.shoot
        console.log(this.losers)
      }

    
    }
  },

 methods: {
    chname: function() {
        // $socket is [WebSocket](https://developer.mozilla.org/en-US/docs/Web/API/WebSocket) instance
        this.$socket.sendObj({cm: 'chname', name: this.nameform})
        
        // or with {format: 'json'} enabled
                        },
  
    give: function() {
        this.$socket.sendObj({cm: 'give'})
         },
    chrole: function(role) {
        this.$socket.sendObj({cm: 'chrole', role: role})
         },
    chshoot: function(index) {
        this.$socket.sendObj({cm: 'chshoot', index: index})
         }
 }

}

</script>

